<template>
  <div class="con">
    <div v-for="(v,k) in info" class="type">
      <div class="title">{{v[0].title}}:</div>

      <div v-for="item in v">
        <div class="card">
          <div class="igb" :class="v[0].grid?'b0':''">
            <div :style="{backgroundImage:'url(' + item.url + ')'}"></div>
          </div>

          <span class="name">{{item.name}}</span>
          <span class="desc">{{item.desc}}</span>
        </div>

      </div>

    </div>
  </div>

</template>

<script>
import { car } from './type'

export default {
  name: 'pankou',
  data() {
    return {
      info: {
        car: car,
        sport_en: [
          { url: require('@/assets/img/adi.jpg'), name: '阿迪达斯', desc: '德国运动品牌，三条纹，三叶草', title: '国外品牌服饰' },
          { url: require('@/assets/img/puma.jpg'), name: '彪马', desc: '德国运动品牌，PUMA，美洲狮' },
          { url: require('@/assets/img/star.jpg'), name: '匡威', desc: '波士顿品牌，主打鞋子' },
          { url: require('@/assets/img/nb.jpg'), name: '新百伦', desc: '波士顿品牌，New Balance' },
          { url: require('@/assets/life/jack.jpg'), name: '杰克琼斯', desc: '丹麦品牌，欧式服饰' },
        ],
        sport_zh: [
          { url: require('@/assets/img/hl.jpg'), name: '海澜之家', desc: '中国江苏，HLA，服饰', title: '国内品牌服饰' },
          { url: require('@/assets/img/peak.jpg'), name: '匹克', desc: '中国福建，PEAK，运动品牌' },
          { url: require('@/assets/img/anta.jpg'), name: '安踏', desc: '中国福建，ANTA，运动品牌' },
          { url: require('@/assets/img/tb.jpg'), name: '特步', desc: '中国福建，XTEP，运动品牌' },
          { url: require('@/assets/img/ning.jpg'), name: '李宁', desc: '中国，LINING，运动品牌' },
          { url: require('@/assets/life/sm.jpg'), name: '森马', desc: '中国，Semir，休闲服饰' },
          { url: require('@/assets/life/ts.jpg'), name: '唐狮', desc: '中国宁波，TONLION，服饰' },
          { url: require('@/assets/life/yc.jpg'), name: '以纯', desc: '中国虎门，YISHION，服饰' },
        ],
        head: [
          { url: require('@/assets/life/hfs.jpg'), name: '海飞丝', desc: '美国宝洁品牌，去屑', title: '洗发水', grid: true },
          { url: require('@/assets/life/pr.jpg'), name: '飘柔', desc: '美国宝洁品牌，女性，美妆' },
          { url: require('@/assets/life/pt.jpg'), name: '潘婷', desc: '美国宝洁品牌，秀发' },
          { url: require('@/assets/life/df.jpg'), name: '多芬', desc: '美国品牌，女性，洗发护发' },
          { url: require('@/assets/life/sx.jpg'), name: '沙宣', desc: '英国洗发品牌，VS' },
          { url: require('@/assets/life/qy.jpg'), name: '清扬', desc: '法国洗发品牌，去屑' },
          { url: require('@/assets/life/shk.jpg'), name: '施华蔻', desc: '德国洗发品牌，美妆' },
          { url: require('@/assets/life/sl.jpg'), name: '舒蕾', desc: '中国品牌，SLEK' },
          { url: require('@/assets/life/sft.jpg'), name: '索芙特', desc: '中国品牌，SOFTTO，美妆' },
        ],
        tooth: [
          { url: require('@/assets/life/jjs.jpg'), name: '佳洁士', desc: '美国宝洁品牌，Crest，氟泰', title: '牙膏', grid: true },
          { url: require('@/assets/life/glj.jpg'), name: '高露洁', desc: '美国品牌，Colgate' },
          { url: require('@/assets/life/hr.jpg'), name: '黑人', desc: '中国上海，DARLIE，清凉' },
          { url: require('@/assets/life/lsl.jpg'), name: '冷酸灵', desc: '中国重庆，抗过敏' },
        ],
        body: [
          { url: require('@/assets/life/sfj.jpg'), name: '舒肤佳', desc: '美国宝洁品牌，抵御细菌', title: '沐浴露', grid: true },
          { url: require('@/assets/life/ls.jpg'), name: '力士', desc: '美国品牌，护理护肤' },
        ],
      }
    }
  },
  methods: {

  },
  mounted() {

  }
}
</script>

<style lang="" scoped>
.con {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.type {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 40px;
  justify-content: center;
  position: relative;
  /* padding-left: 140px; */
}

.title {
  padding-right: 20px;
  width: 140px;
  position: absolute;
  left: 20px;
  top: -30px;
}

.card {
  width: 145px;
  height: 145px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
  margin-bottom: 10px;
  border: 1px solid #cdcdcd;
  padding: 10px;
  border-radius: 10px;
}

.name {
  padding: 2px;
}

.desc {
  color: #999;
  font-size: 14px;
  white-space: pre-wrap;
}

.igb > div {
  width: 60px;
  height: 60px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.igb {
  border-radius: 50%;
  overflow: hidden;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.b0 {
  border-radius: 0;
}
</style>


