<template>
  <div class="con">
    <div v-for="(v,k) in info" class="type">
      <div class="title">{{v[0].title}}:</div>

      <div v-for="item in v">
        <div class="card">
          <div class="igb">
            <div :style="{backgroundImage:'url(' + item.url + ')'}"></div>
          </div>

          <span class="name">{{item.name}}</span>
          <span class="desc">{{item.desc}}</span>
        </div>

      </div>

    </div>
  </div>

</template>

<script>
export default {
  name: 'bat',
  data() {
    return {
      info: {
        car: [
          { url: require('@/assets/cartoon/fm.jpg'), name: 'FANMA', desc: '年轻气盛', title: '刃牙二' },
          // { url: require('@/assets/img/lei.jpg'), name: '雷克萨斯', desc: '日本丰田汽车旗下豪华品牌，均价30w' },

        ],

      }
    }
  },
  methods: {

  },
  mounted() {

  }
}
</script>

<style lang="" scoped>
.con {
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
}

.type {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
  padding-left: 140px;
}

.title {
  padding-right: 20px;
  width: 140px;
  position: absolute;
  left: 20px;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 40px;
  margin-bottom: 20px;
  border: 1px solid #cdcdcd;
  padding: 10px;
  border-radius: 10px;
}

.name {
  padding: 10px;
}

.desc {
  color: #999;
  font-size: 14px;
  white-space: pre-wrap;
}

.igb > div {
  width: 140px;
  height: 140px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.igb {
  border-radius: 50%;
  overflow: hidden;
  width: 140px;
  height: 140px;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>


