<template>
  <div class="container">
    <div class="box" v-for="item in data" :key="item.id">
      <div class="first">
        <p>{{item.name}}</p>
        <div class="desc">{{item.desc}}</div>
      </div>
      <div class="last" :style="{backgroundImage:'url('+item.src+')'}"> </div>
    </div>
  </div>
</template>

<script>
export default {
  name: '',
  data() {
    return {
      data: [
        { name: '一念永恒', desc: '改编自耳根笔下的修仙题材小说。一股清流,以反套路修仙和沙雕欢乐为主。', src: require('@/assets/img/bxc.jpg'), id: 'bxc' },
        { name: '一念永恒', desc: '改编自耳根笔下的修仙题材小说。一股清流,以反套路修仙和沙雕欢乐为主。', src: require('@/assets/img/bxc.jpg'), id: 'bxc' },
        { name: '一念永恒', desc: '改编自耳根笔下的修仙题材小说。一股清流,以反套路修仙和沙雕欢乐为主。', src: require('@/assets/img/bxc.jpg'), id: 'bxc' },
        { name: '一念永恒', desc: '改编自耳根笔下的修仙题材小说。一股清流,以反套路修仙和沙雕欢乐为主。', src: require('@/assets/img/bxc.jpg'), id: 'bxc' },
      ]
    }
  },
  methods: {

  }
}
</script>

<style lang="" scoped>
.container {
  display: flex;
}

.box {
  width: 300px;
  height: 200px;
  border: 1px solid #e7e7e7;
  border-radius: 4px;
  display: flex;
  margin: 10px;
  padding: 10px;
}

.first {
  flex: 1;
}
.desc {
  font-size: 14px;
  color: #999;
  text-align: left;
  text-indent: 2em;
  margin-top: 10px;
  padding: 0 8px;
}
.last {
  flex: 1;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  cursor: pointer;
}
</style>


