<template>
  <div class="container">
    <Input v-model="a" :rows='10' type="textarea" placeholder="格式化文档" class="paiban-inp" @on-enter="deal" @on-blur="deal" />
    <div class="btnbox">
      <Button type="primary" @click="deal">一键处理</Button>
      <Button @click="reset">重置</Button>
    </div>
  </div>
</template>

<script>
export default {
  name: '',
  data() {
    return {
      a: ''
    }
  },
  methods: {
    reset() {
      this.a = ''
    },

    deal() {
      // [! ! 。？]
      this.a = this.a.replace(/！/g, '\n')
        .replace(/!/g, '\n')
        .replace(/。”/g, '”\n')
        .replace(/。/g, '\n')
        .replace(/？/g, '\n')
        .replace(/\?/g, '\n')
        .replace(/，“/g, '\n“')
        .replace(/；/g, '\n')
        .replace(/——/g, '\n')

    },

  }
}
</script>

<style lang="" scoped>
.container {
  width: 60%;
  margin: 0 auto;
}

.btnbox {
  margin: 20px;
}
</style>


